<template>
  <a-affix :offset-top="0" class="header-affix">
    <!-- headerBgClass, -->
    <div :class="[headerBgClass,'header-bg-class']">
     <div :class="[isProgramme&&'layout-header-programme','layout-header']" >
      <div class="layout-header-left">
        <div class="logo-info">
          <img src="@/assets/logo.png" alt class="logo" v-show="!isProgramme"/>
          <img src="@/assets/logo1.png" alt class="logo" v-show="isProgramme"/>
        </div>
        <!-- horizontal -->
        <div class="menu-box">
          <a-menu v-model="current" mode="horizontal" @click="event=>handleClick(event)">
            <template v-for="(item) of navLists">
              <a-menu-item :key="item.key" v-if="!item.type">{{item.tab}}</a-menu-item>
              <a-sub-menu :key="item.key" @titleClick="titleClick" v-if="item.type">
                <span slot="title">
                  <span>{{item.tab}}</span>
                </span>
                <a-menu-item :key="val.key" v-for="val of item.list">{{val.tab}}</a-menu-item>
              </a-sub-menu>
            </template>
          </a-menu>
        </div>
      </div>
      <div class="layout-header-right">
        <a-button  shape="round" @click="btnLogin" class="login">登录</a-button>
        <a-button type="primary" shape="round" @click="btnLogin">免费试用</a-button>
      </div>
     </div> 
    </div>

  </a-affix>
</template>
<script>
export default {
  name: "navHeader",
  data() {
    return {
      timer:null,
      headerBgClass:'',
      isProgramme:false,
      hasLogin: false,
      logoPath:'@/assets/logo.png',
      
      accountLoginBaseInfo: {},
      current: ["1"],
      navLists: [
        { tab: "首页", key: "1", path: "/index" },
        // 2,3,4,5,6
        // 7
        {
          tab: "产品",
          key: "2",
          type:"select",
          path: "/product",
          list: [
            { key: "3", tab: "IM客服系统",query:{ category:'1' } },
            { key: "4", tab: "直播短视频",query:{ category:'2' } },
            { key: "5", tab: "智能SCRM",query:{ category:'3' } },
            { key: "6", tab: "社交电商",query:{ category:'4' } },
          ],
        },      
        {
          tab: "解决方案",
          key: "7",
          type:"select",
          path: "/programme",
          list: [
            { key: "13", tab: "产业链服务平台",query:{ category:'1' } },
          ],
        },        
        {
          tab: "系统特色",
          key: "8",
          type:"select",
          path: "/system",
          list: [
            { key: "9", tab: "系统概述",query:{ category:'1' } },
            { key: "10", tab: "特色功能",query:{ category:'2' } },
          ],
        },
        { tab: "价格", key: "11", path: "/price" },
        { tab: "渠道", key: "12", path: "/channel" },
      ],
    };
  },
  created() {
    let navLists = this.navLists;
    let pathInfo = navLists.find(element=>{
      return element.path === this.$route.path
    });
    if(pathInfo.type==='select') {
      let queryList = pathInfo.list;
      let queryInfo = queryList.find(element=>{
        return element.query.category === this.$route.query.category;
      })
      this.current = [`${queryInfo.key}`];
    } else {
      this.current = [`${pathInfo.key}`];
    }
    this.isProgramme = this.$route.path=='/programme'?true:false;
  },
  mounted() {
    window.addEventListener('scroll', this.pageScroll)
  },
  watch:{
    $route:{
      handler(newVal) {
        this.isProgramme = newVal.path=='/programme'?true:false;
      },
      deep:true
    }
  },
  methods: {
    pageScroll() {
      var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
      if(this.timer) {clearTimeout(this.timer)}
      this.timer = setTimeout(()=>{
        if(scrollTop>100) {
          this.headerBgClass = this.isProgramme?"header-programme-bgclass":"header-common-bgclass";
        }else {
          this.headerBgClass = "";
        }
      },10)

    },
    handleClick(event) {
      let current = this.current;
      let navLists = this.navLists;
      // keyPath
      let keyPath = event.keyPath;
      // 存在下拉选项时;
      console.log("当前的event", current,event,keyPath,navLists);
      console.log("当前的event-----99999999",event);
      // if(current.includes(event.key)) { return }
      let navInfo = navLists.find(element=>{
        return element.key === keyPath[keyPath.length-1];
      });
      // console.log('当前的keyPath',keyPath);
      this.current = keyPath[0];
      console.log('navInfo',navInfo)
      // if(navInfo && navInfo.list&&) {
      // }
      let queryInfo = {};
      if(event.key!=navInfo.key) {
        queryInfo = navInfo.list.find(element=>{ return element.key=== event.key});
        console.log('处于二级路由下11111111111111',queryInfo);
      }
      this.$router.push({
        path: navInfo.path,
        query: queryInfo.query||{}
      });
    },
    titleClick(event) {
      console.log("titleClick", event);
    },
    logout() {
      localStorage.removeItem("accountLoginBaseInfo");
      sessionStorage.clear();
      this.$router.push({ path: "/login" });
      location.reload();
    },
    btnLogin() {
      console.log('当前的登陆信息',this.$route.path);
      window.location.href = "http://crm.haomp.top";
    },
    
  },
};
</script>

<style scoped lang="scss">
.layout-header {
  height: 60px;
  display: flex;
  width: 1200px;
  margin: 0 auto;
  justify-content: space-between;
  align-items: center;
  .layout-header-left {
    display: flex;
    align-items: center;
    .logo-info {
      display: flex;
      height: 60px;
      margin-right: 37px;
      align-items: center;
      .logo {
        width: 90px;
        margin-right: 6px;
      }
      .company-info {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        line-height: 20px;
        .company-name {
          font-size: 18px;
          font-weight: bold;
          color: #333;
        }
        .company-desc {
          font-size: 14px;
          font-family: PingFangSC-Regular, PingFang SC;
          font-weight: 400;
          color: #888888;
        }
      }
    }
    /deep/ .ant-menu-horizontal{
      border-bottom-color:transparent;
      background-color:transparent ;
    }
  }
  .layout-header-right {
    display: flex;
    align-items: center;
    img {
      width: 32px;
      height: 32px;
      border-radius: 50%;
      margin-left: 15px;
    }
    img.workbench{
      width: 20px;
      height: 20px;
    }
    img.head-right-icon {
      vertical-align: middle;
      height: 20px;
      width: 20px;
      border-radius: 0;
      cursor: pointer;
    }
    .login{
      margin-right: 10px;
    }
  }
}
.layout-header-programme {
  /deep/ .ant-menu-submenu-title{
    color:#fff;
  }
  /deep/ .ant-menu-item{
    color:#fff;
  }
}
/deep/ .ant-layout-header{
  padding:0;
}
/deep/ .ant-affix{
  width: 100%!important;
  min-width: 1200px;
}

.header-bg-class{
  width: 100%;
  min-width: 1200px;
}

.header-common-bgclass{
  background-color: white;
  box-shadow: rgba(26, 26, 51, 0.05) 0px 8px 16px 0px;
}

.header-programme-bgclass{
  background: #102134;
}
</style>